import React, { useMemo } from "react";
import { HardwiredProviderType } from "../types/index";
import { createConfig, getContext } from "../config";

/**
 * React Context Provider for Hardwired
 * @param children
 * @param config Hardwired configuration object (UserConfig)
 * @constructor
 */
export const Provider = ({
  children,
  config,
}: HardwiredProviderType): JSX.Element => {
  const Context = useMemo(getContext, [config]);
  const expandedConfig = useMemo(() => createConfig(config), [config]);

  return <Context.Provider value={expandedConfig}>{children}</Context.Provider>;
};

export default { Provider };
